import { Component, PLATFORM_ID, Inject } from '@angular/core';
import { isPlatformBrowser } from '@angular/common';
import { LoadingBarService } from '@ngx-loading-bar/core';
import { map, delay, withLatestFrom } from 'rxjs/operators';
import { TranslateService } from '@ngx-translate/core';
import { ApiCartService } from './shared/api-services/cart.service';
import { ApiProductService } from './shared/api-services/product.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  
  // For Progressbar
  loaders = this.loader.progress$.pipe(
    delay(1000),
    withLatestFrom(this.loader.progress$),
    map(v => v[1]),
  );
  
  constructor(
    @Inject(PLATFORM_ID) private platformId: Object,
    private loader: LoadingBarService, translate: TranslateService,
    private apiCartService: ApiCartService, private apiProductService: ApiProductService
  ) {
    if (isPlatformBrowser(this.platformId)) {
      translate.setDefaultLang('en');
      translate.addLangs(['en', 'fr']);
    }

    document.documentElement.style.setProperty('--theme-deafult', '#0fb3db');
    this.apiCartService.getCart().subscribe(() => {});
    this.apiProductService.getCategories().subscribe(() => {});
    this.apiProductService.getProducts().subscribe(() => {});
  }

}
